<template>

   <div class="brandPage">
         <header class="top-hd">
            <div class="in-hd">
                <a href="javascript:;" class="back-btn" v-link="{path:'/index'}"></a>
                <h1 class="title">选择品牌</h1>
            </div>
        </header>
        <section class="main-content mer" style="background:#fff;">
            <div class="inner-content">
                <autocomplete :suggestions="sorts" :selection.sync="value" sec="brand"></autocomplete>
                </autocomplete>
            </div>
        </section>
    </div>



</template>


<script>
    
   import autocomplete from './autocomplete.vue';
    
    export default{
        
       
    
        data(){
        
            return{
                
                sorts:[
                    
                    "Samsung","Apple","Xiaomi","Huawei","ZhongXing","KuPai","Nubiya","Nuojiya","Heimei"
                ]
            }
        },
         props:["sec"],
         components:{
            
            autocomplete
        }
    }
</script>
<style>
/*    .dropdown-menu{display: none;}*/
    .open>.dropdown-menu {
    display: block;
    }
</style>


